import { Box, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";

const FooterOnPage = () => {
    //eslint-disable-next-line
    const history = useNavigate();
    
    const path = window.location.pathname;

    return (
        <Box component="footer" sx={{ my: 1, mb: 3, px: 5 }}>
            <Box className="flex-column note mt-1 aic_f">
                {path === '/time'
                    ?    <Box className="flex-row jcc">
                            <Typography variant="subtitle1">
                                created by&nbsp;
                                <a href="https://github.com/roso1nik" target="_blank" rel="noreferrer noopener">
                                    roso1nik
                                </a>
                                &nbsp;with love ❤ // v1.1
                            </Typography>
                        </Box>
                    :   <Box className="flex-row">
                            <Typography variant="subtitle1">
                                created by&nbsp;
                                <a href="https://github.com/roso1nik" target="_blank" rel="noreferrer noopener">
                                    roso1nik
                                </a>
                                &nbsp;with love ❤ // v1.1
                            </Typography>
                        </Box>
                }
            </Box>
        </Box>
    )
}

export default FooterOnPage;