import { Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import { STARTPAGE_ROUTE } from "../../../../routes/consts";
import ChangeTheme from "../../../UI/Changers/ChangeTheme";

const TimePage = () => {
    const history = useNavigate();

    const saveDATA = new Date();
    const [time, setTime] = useState(`${saveDATA.getHours() < 10 ? `0${saveDATA.getHours()}` : saveDATA.getHours()}:${saveDATA.getMinutes() < 10 ? `0${saveDATA.getMinutes()}` : saveDATA.getMinutes()}:${saveDATA.getSeconds() < 10 ? `0${saveDATA.getSeconds()}` : saveDATA.getSeconds()}`);
    const [date, setDate] = useState(`${saveDATA.getDate() < 10 ? `0${saveDATA.getDate()}` : saveDATA.getDate()}.${saveDATA.getMonth() + 1 < 10 ? `0${saveDATA.getMonth() + 1}` : saveDATA.getMonth() + 1}.${saveDATA.getFullYear() < 10 ? `0${saveDATA.getFullYear()}` : saveDATA.getFullYear()}`);
    const getTime = () => {
        const t = new Date();
        setTime(`${t.getHours() < 10 ? `0${t.getHours()}` : t.getHours()}:${t.getMinutes() < 10 ? `0${t.getMinutes()}` : t.getMinutes()}:${t.getSeconds() < 10 ? `0${t.getSeconds()}` : t.getSeconds()}`);
    }
    const getDate = () => {
        const t = new Date();
        setDate(`${t.getDate() < 10 ? `0${t.getDate()}` : t.getDate()}.${t.getMonth() + 1 < 10 ? `0${t.getMonth() + 1}` : t.getMonth() + 1}.${t.getFullYear() < 10 ? `0${t.getFullYear()}` : t.getFullYear()}`);
    }
    setInterval(getTime, 10);
    setInterval(getDate, 100);
    
    return (
        <>
            <Box className="time-page flex-column">
                <Typography 
                    variant="h1" 
                >
                    {time}
                </Typography>
                <Typography 
                    variant="h3" 
                    className="note"
                >
                    {date}
                </Typography>
            </Box>
            <Box className="flex-row jcc">
                <Button
                    onClick={() => history(STARTPAGE_ROUTE)}
                    className="note"
                    startIcon={<ArrowBackIosIcon />}
                >
                    back
                </Button>
                <ChangeTheme />
            </Box>
        </>
    )
}

export default TimePage;