import React from "react";
import { Box, Button, Tooltip, Typography } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { projects } from "../../../static/steps/senior.js"

const SeniorStep = () => {

    const history = useNavigate();

    return (
        <Box className="flex-column block" sx={{ pb: 4 }}>
            <Box className="flex-row wrap">
                <Typography variant="h2">
                    Senior Projects
                </Typography>
            </Box>
            <Box className="flex-row jcsb wrap gap-1" sx={{ mt: 3 }}>
                {Object.keys(projects).map((el) => (
                    <React.Fragment key={Math.random()}>
                        {projects[el][2]
                            ?   <Tooltip title="Soon. Work in progress" arrow>
                                    <span className="button-PROJ">
                                        <Button 
                                            onClick={() => history(projects[el][1])}
                                            variant="contained"
                                            key={Math.random()}
                                            disableElevation
                                            disabled={projects[el][2] || false}
                                        >
                                            {projects[el][0]}
                                        </Button>
                                    </span>
                                </Tooltip>
                            :   <Button 
                                    onClick={() => history(projects[el][1])}
                                    variant="contained" 
                                    className="button-PROJ" 
                                    key={Math.random()}
                                    disableElevation
                                    disabled={projects[el][2] || false}
                                >
                                    {projects[el][0]}
                                </Button>
                        }
                    </React.Fragment>
                ))}
            </Box>
        </Box>
    )
}

export default SeniorStep;