import { Button, Divider, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext, useState } from "react";
import { appContext } from "../../../../context/index";
import AppBar from "../../../UI/AppBar";
import InputSlider from "../../../UI/Slider/Slider";

const PasswordGenerate = () => {

    const { passSymbols } = useContext(appContext);

    const [passIsHide, setPassIsHide] = useState(false);
    const changeVisibility = () => {
        if (passIsHide) {
            setPassIsHide(false);
            return;
        }
        if (passIsHide === false) setPassIsHide(true);
    }

    const [passwordText, setPassworsText] = useState('<3');
    let password;
    const generatePass = (pLength) => {
        password = '';
        pLength = passSymbols;
        var keyListAlpha="abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ",
            keyListInt="1234567890",
            keyListSpec="!@#_%*-?$^"
        var len = Math.ceil(pLength/2);
        len = len - 1;
        var lenSpec = pLength-2*len;
        for (let i=0;i<len;i++) {
            password+=keyListAlpha.charAt(Math.floor(Math.random()*keyListAlpha.length));
            password+=keyListInt.charAt(Math.floor(Math.random()*keyListInt.length));
        }
        for (let i=0;i<lenSpec;i++) {
            password+=keyListSpec.charAt(Math.floor(Math.random()*keyListSpec.length));
        }
        password=password.split('').sort(function(){return 0.5-Math.random()}).join('');
        setPassworsText(password);
    }

    return (
        <Box component="main">
            <AppBar />
            <Box className="flex-column gap-1 block">
                <Box>
                    <Typography variant="h2">
                        Password Generator
                    </Typography>
                </Box>
                <Box>
                    {passIsHide
                        ?   <Typography 
                                variant="h4" 
                                className="blur"
                            >
                                {passwordText}
                            </Typography>
                        :   <Typography 
                                variant="h4" sx={{ fontWeight: 300, overflowX: "scroll" }}
                            >
                                {passwordText}
                            </Typography>
                    }
                    <Box className="flex-row gap-1" sx={{ mt: 1 }}>
                        <Button 
                            onClick={() => {navigator.clipboard.writeText(passwordText)}}
                            className="note"
                        >
                            copy
                        </Button> 
                        <Button 
                            onClick={() => changeVisibility()}
                            className="note"
                        >
                            {passIsHide
                                ?   'show'
                                :   'hide'
                            }
                        </Button>
                    </Box>
                </Box>
                <Box className="flex-column wfull jcc">
                    <Divider flexItem sx={{ my: 2 }} />
                    <InputSlider />
                    <Button 
                        onClick={() => generatePass()}
                        variant="contained"
                        sx={{ mt: 1 }}
                        disableElevation
                    >
                        Generate
                    </Button>
                </Box>
            </Box>
        </Box>
    )
}

export default PasswordGenerate;