import React, { useState } from 'react';
import { BrowserRouter } from 'react-router-dom'
import AppRouter from './components/AppRouter';
import { Container } from '@mui/material';
import { appContext } from "./context/index";
import FooterOnPage from './components/UI/FooterOnPage';


const App = () => {
  
  let pathnow = window.location.pathname;
  const [tasks, setTasks] = useState([]);
  const [theme, setTheme] = useState(false); // false - light, true - dark
  const [passSymbols, setPassSymbols] = useState(12);
  const [pagePathNow, setPagePathNow] = useState(pathnow);
  const [questionNow, setQuestionNow] = useState(1);
  const [correctAnswers, setCorrectAnswers] = useState(0);
  const [valueProgress, setValueProgress] = useState(0);
  const [quizIsOver, setQuizIsOver] = useState(false);

  return (
    <appContext.Provider value={{
      tasks, setTasks,
      theme, setTheme,
      passSymbols, setPassSymbols,
      pagePathNow, setPagePathNow,
      questionNow, setQuestionNow,
      correctAnswers, setCorrectAnswers,
      valueProgress, setValueProgress,
      quizIsOver, setQuizIsOver
    }}>
      <BrowserRouter>
        {!theme
          ? <style>:root {"{--font: 'Inter', sans-serif !important;--color: #000;--colorOpacity: #000000e0;--colorInvert: #fff;--colorAction: #7bb0ff;--colorDelete: #e63946;--colorWhite: #fff;--helpColor: #c4c3c6; --bgBody: #fff;--bgSecond: #f9f9f9;--bgSecondHelper: rgba(0, 0, 0, 0.06);--sliderBg: #e5e5e5;}"}</style>
          : <style>:root {"{--font: 'Inter', sans-serif !important;--color: #fff;--colorOpacity: #ffffffe0;--colorInvert: #000;--colorAction: #7bb0ff;--colorDelete: #e63946;--colorWhite: #fff;--helpColor: #c4c3c6; --bgBody: #101010;--bgSecond: #181818;--bgSecondHelper: rgba(0, 0, 0, 0.75);--sliderBg: #e5e5e5;}"}</style>
        }
        <Container maxWidth="lg">
          <AppRouter />
          <FooterOnPage />
        </Container>
      </BrowserRouter>
    </appContext.Provider>
  );
}

export default App;