import { Box, Button, IconButton, TextField, Tooltip, Typography } from "@mui/material";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { appContext } from "../../../../context";
import DeleteIcon from '@mui/icons-material/Delete';
import AlertCopyTasks from "../../../UI/Alerts/AlertCopyTasks";
import AppBar from "../../../UI/AppBar";


const ToDoPage = () => {
    // eslint-disable-next-line
    const history = useNavigate();

    const { tasks, setTasks } = useContext(appContext);

    const [value, setValue] = React.useState('');
    const handleChange = (event) => {
        setValue(event.target.value);
    };

    const createTask = () => {
        if (value !== '') {
            setTasks([...tasks, value]);
            setValue(''); 
        }
    }

    const deleteTask = (el) => {
        setTasks([
            ...tasks.slice(0, el),
            ...tasks.slice(el + 1)
        ]);
    }

    return (
        <Box component="main">
            <AppBar />
            <Box className="flex-column wfull block gap-1">
                <Box className="flex-row jcc">
                    <Typography variant="h2">
                        To Do List
                    </Typography>
                </Box>
                <Box className="flex-row gap-0">
                    <TextField 
                        value={value}
                        onChange={handleChange}
                        className="wfull input-addTask"
                        label="Add Task Here" 
                        variant="filled"
                    />
                    <Button 
                        onClick={() => createTask()}
                        variant="contained" 
                        sx={{ height: 56 }}
                        disableElevation
                    >
                        Add
                    </Button>
                </Box>
            </Box>
            <Box className="flex-column gap-0">
                <Box className="flex-column wfull block gap-1">
                    {tasks.length !== 0
                        ?   <>
                                {tasks.map(task => (
                                    <Box component="span" className="flex-row gap-1 jcsb task" key={Math.random()}>
                                        <Typography>
                                            {tasks.indexOf(task) + 1}.&nbsp;{task}
                                        </Typography>
                                        <Tooltip title="Delete Task" arrow>
                                            <IconButton onClick={() => deleteTask(tasks.indexOf(task))}>
                                                <DeleteIcon className="button-delete" />
                                            </IconButton>
                                        </Tooltip>
                                    </Box>
                                ))}
                            </>
                        :   <Typography 
                                variant="h6"
                                className="flex-row jcc wfull note"
                                sx={{ fontWeight: 300 }}
                            >
                                No Tasks Found!
                            </Typography>
                    }
                </Box>
                <Box className="flex-row gap-1">
                    <AlertCopyTasks />
                </Box>
            </Box>
        </Box>
    )
}

export default ToDoPage;