import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import { appContext } from '../../../context';
import { Typography } from '@mui/material';
import { Box } from '@mui/system';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertCopyTasks() {
  const [open, setOpen] = React.useState(false);

  const { tasks } = React.useContext(appContext);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button 
        variant="link" 
        className="note" 
        onClick={handleClickOpen}
      >
        Get Text File
      </Button>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
      >
        <DialogTitle>{"Your tasks in text"}</DialogTitle>
        <DialogContent sx={{ minWidth: 600 }}>
            {tasks.length !== 0
                ?   <>
                      {tasks.map(task => (
                        <Box key={Math.random()}>{tasks.indexOf(task) + 1}.&nbsp;{task}</Box>
                      ))} 
                    </>
                :   <Typography 
                      variant="h6"
                      className="flex-row jcc wfull note"
                      sx={{ fontWeight: 300 }}
                    >
                      No Tasks Found!
                    </Typography>
            }
        </DialogContent>
        <DialogActions>
          {tasks.length !== 0
            ? <Button onClick={() => {navigator.clipboard.writeText(tasks)}}>Copy</Button>
            : <Button disabled>Copy</Button>
          }
          <Button variant="contained" onClick={handleClose} disableElevation>Okay</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}