import { Button, Tooltip, Typography } from "@mui/material";
import React, { useState } from "react";
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import { useNavigate } from "react-router-dom";
import { TIMEPROJECT_ROUTE } from "../../../routes/consts";

const TimeWidget = () => {
    const history = useNavigate();

    const [time, setTime] = useState(NaN);
    const getTime = () => {
        const t = new Date();
        setTime(`${t.getHours() < 10 ? `0${t.getHours()}` : t.getHours()}:${t.getMinutes() < 10 ? `0${t.getMinutes()}` : t.getMinutes()}:${t.getSeconds() < 10 ? `0${t.getSeconds()}` : t.getSeconds()}`);
    }
    setInterval(getTime, 1);
    

    return (
        <Tooltip title="Time Project" arrow>
            <Button onClick={() => history(TIMEPROJECT_ROUTE)}>
                <Typography 
                    variant="subtitle1" 
                    className="flex-row note gap-0"
                >
                    <AccessTimeIcon />
                    {time}
                </Typography>
            </Button>
        </Tooltip>
    )
}

export default TimeWidget;