import { Button, IconButton, LinearProgress, Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import AppBar from "../../../UI/AppBar";
import { quiz } from "../../../../static/quiz/quiz";
import { appContext } from "../../../../context";
import RestartAltIcon from '@mui/icons-material/RestartAlt';


const QuizApp = () => {

    const { questionNow, setQuestionNow, correctAnswers, setCorrectAnswers, valueProgress, setValueProgress, quizIsOver, setQuizIsOver } = useContext(appContext);

    const countOfQuestions = Object.keys(quiz).length;
    const step = 100 / countOfQuestions;

    const replyQuestion = (i) => {
        const correct = quiz[questionNow].correct_var;
        const answer = i;
        if (correct === answer) {
            let plusSave = correctAnswers + 1;
            setCorrectAnswers(plusSave);
        }

        let plusSave = questionNow + 1;
        setQuestionNow(plusSave);

        if (valueProgress < 100) {
            let progressSave = valueProgress + step;
            setValueProgress(progressSave);
        }
        if (questionNow === countOfQuestions) {
            setQuizIsOver(true);
        }
    }

    const restartQuiz = () => {
        setQuizIsOver(false);
        setQuestionNow(1);
        setCorrectAnswers(0);
        setValueProgress(0);
    }

    return (
        <Box component="main" id="quiz-app">
            <Box className="flex-row jcsb ">
                <AppBar /> 
                <Box sx={{ px: 1 }}>
                    <Tooltip title="Restart Quiz" arrow>
                        <IconButton 
                            onClick={() => restartQuiz()}
                            className="note"
                        >
                            <RestartAltIcon />
                        </IconButton>
                    </Tooltip>
                </Box>
            </Box>
            <Box className="flex-column block">
                {!quizIsOver
                    ?   <>
                            <Box className="flex-row jcc" sx={{ my: 3 }}>
                                <LinearProgress 
                                    variant="determinate" 
                                    value={valueProgress} 
                                    className="wfull"
                                />
                            </Box>
                            <Box className="flex-row">
                                <Typography variant="h2">
                                    {quiz[questionNow].title}
                                </Typography>
                            </Box>
                            <Box className="flex-column wfull gap-0 button-answers" sx={{ mt: 3 }}>
                                {quiz[questionNow].variants.map(el => (
                                    <Button 
                                        onClick={() => replyQuestion(quiz[questionNow].variants.indexOf(el) + 1)}
                                        variant="outlined" 
                                        key={quiz[questionNow].variants.indexOf(el) + 1}
                                    >
                                        {el}
                                    </Button>
                                ))}
                            </Box>
                        </>
                    :   <>
                            <Box className="flex-column jcc acc">
                                <img src="https://cdn-icons-png.flaticon.com/512/2278/2278992.png" width="256px" alt="end" />
                                <Typography variant="h4" sx={{ mt: 2 }}>
                                    You answered {correctAnswers} out of {countOfQuestions} correctly
                                </Typography>
                                <Button 
                                    onClick={() => restartQuiz()}
                                    variant="contained"
                                    disableElevation
                                    sx={{ fontSize: 18, mt: 2, borderRadius: 2 }}
                                >

                                    Try Again
                                </Button>
                            </Box>
                        </>
                }
            </Box>
        </Box>
    )
}

export default QuizApp;