import React from 'react';
import { ERROR_ROUTE, PASSPROJECT_ROUTE, QUIZPROJECT_ROUTE, STARTPAGE_ROUTE, TIMEPROJECT_ROUTE, TODOPROJECT_ROUTE } from "./consts";
import ErrorPage from "../components/pages/ErrorPage";
import Start from "../components/pages/Start";
import ToDoPage from '../components/pages/Projects/Beginner/ToDoPage';
import TimePage from '../components/pages/Projects/Beginner/TimePage';
import PasswordGenerate from '../components/pages/Projects/Beginner/PasswordGenerate';
import QuizApp from '../components/pages/Projects/Beginner/QuizApp';

export const publicRoutes = [
    {
        path: STARTPAGE_ROUTE,
        Component: <Start />
    },
    {
        path: ERROR_ROUTE,
        Component: <ErrorPage />
    },
    {
        path: TODOPROJECT_ROUTE,
        Component: <ToDoPage />
    },
    {
        path: TIMEPROJECT_ROUTE,
        Component: <TimePage />
    },
    {
        path: PASSPROJECT_ROUTE,
        Component: <PasswordGenerate />
    },
    {
        path: QUIZPROJECT_ROUTE,
        Component: <QuizApp />
    }
]