import { Button } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useNavigate } from "react-router-dom";
import { STARTPAGE_ROUTE } from "../../routes/consts";
import TimeWidget from "./Widgets/TimeWidget";
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ChangeTheme from "./Changers/ChangeTheme";
// eslint-disable-next-line
import ChangeLanguage from "./Changers/ChangeLanguage";

const AppBar = () => {
    const history = useNavigate();
    const path = window.location.pathname;
    return (
        <Box component="header" className="flex-row gap-0 wrap" sx={{ px: 5 }}>
            {path === '/'
                ?   <></>
                :   <Button 
                        onClick={() => history(STARTPAGE_ROUTE)}
                        variant="link" 
                        startIcon={<ArrowBackIosIcon />} 
                        className="note min-width"
                    >
                        back
                    </Button>    
            }
            <TimeWidget />
            <ChangeTheme />
            {/* <ChangeLanguage /> */}
        </Box>
    )
}

export default AppBar;